

.bidding-container{
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
}

.biddedArtwork{
    position: relative;
}
.myBids-page .title{
    font-size: 36px;
    line-height: 1.15;
    letter-spacing: -0.02em;
    font-weight: 600;
    text-align: center;
    margin-bottom: 4%;
}
.biddedArtwork .status{
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 10;
    background: #000;
    color: #fff;
    padding: 10px;
}

.biddedArtwork .bidding-action{
    background: #4e61ff;
    display: flex;
    justify-content: space-between;
    padding: 2%;
    margin-bottom: 15%;

}


.biddedArtwork .bidding-btn{
    background: #fff;
    padding: 2% 0;
    border-radius: 25px;
    width: 20%;
    text-align: center;
}

