.main-countdown{
    display: flex;
    justify-content: flex-start;
    width: 100%;
}

.main-countdown .item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 20px 0 0;
}
.main-countdown .item .item-bold{
    font-size: 2em;
    font-weight: 600;
}

.main-countdown .item .item-fade{
    font-size: 16px;
    font-weight: 600;
    color: #666666;
}



.mini-countdown{
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(3,36px);
}
.mini-countdown .item span{
   font-size: 18px;
   font-weight: 600;
}
.mini-countdown .item span span{
    font-size: 16px;
}