.artworkInput-container {
    height: 50vh;
}

/* #artworkInput {
    display: none;
} */

.mint-sideContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mint .title {
    font-size: 24px;
    text-align: center;
    font-weight: 600;
    padding: 5px 0 40px 0;
    margin-bottom: 20px;
}
.mint .img {
   width: auto;
}

.mint .uploadBtn{
    margin-top: 10px;
    text-align: center;
    font-weight: 600;
    width: 100%;
    padding: 16px 24px;
    cursor: pointer;
    color: #FFFFFF;
    box-shadow: rgb(0 0 0 / 10%) 0px 10px 20px;
    border-radius: 12px;
    border: none;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    font-size: 18px;
    background-image: linear-gradient(to right, #2ac0ff, #4867ff);
 
}

