.tranasction {
    display: grid;
    gap: 10px;
    grid-template-columns: 36px 4fr 2fr;
    padding: 16px 10px;
    border-radius: 10px;
    -webkit-box-align: center;
    align-items: center;
    position: relative;
    background-color: var(--theme-ui-colors-white-100, #FFFFFF);
}
.tranasction:before {
    content: "";
    position: absolute;
    inset: 0px;
    z-index: -1;
    box-shadow: rgb(0 0 0 / 5%) 0px 10px 20px;
    border-radius: 10px;
}

.tranasction .avatar-image {}

.tranasction .avatar-image .img {
    border-radius: 30px;
}

.tranasction .description {}


.tranasction .price {
    display: grid;
    gap: 16px;
    grid-template-columns: auto 16px;
    -webkit-box-align: center;
    align-items: center;
}

@media screen and (min-width: 52em) {
    .tranasction {
        padding-left: 24px;
        padding-right: 24px;
    }
}

@media screen and (min-width: 40em) {
    .tranasction {
        padding-left: 16px;
        padding-right: 16px;
        gap: 16px;
    }
    .tranasction .price {
        gap: 24px;
    }
}

@media screen and (min-width: 52em) {
    .tranasction {
        grid-template-columns: 44px 4fr 2fr;
    }
}