
.form-control-o{
    position: relative;
    margin-bottom: 15px;
    width: 100%;
}
.form-control-o label{
    display: none;
}
.form-control-o input{
    display: block;
    width: auto;
    padding: 2px 20px;
    line-height: 1;
    border: none;
    transition: box-shadow 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    background-color: #FFFFFF;
    min-height: 60px;
    border-radius: 10px;
    box-shadow: rgb(230 230 230) 0px 0px 0px 1px inset, rgb(0 0 0 / 5%) 0px 10px 20px;
}
.form-control-o button{
    width: 100%;
    /* background: aqua; */
    background-image: linear-gradient(to right, #2ac0ff, #4867ff);
    padding: 3% 0;
    color: #fff;
    font-size: 18px;
    border: none;
    font-weight: 600;
}

.dailog-containter{
    width: 100%;
    text-align: center;
    padding: 4%;
    
}
.dailog-containter .alert{
    padding: 4%;
    
}
.dailog-containter .error-alert{
    color: #842029;
    background-color: #f8d7da;
    border-color: #f5c2c7;
    
}

.dailog-containter .success-alert{
    color: #0f5132;
    background-color: #d1e7dd;
    border-color: #badbcc;
    
}

