.bid-container{
    width: 100%;
    max-width: 1600px;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
}
.bid-container .title{
    box-sizing: border-box;
    margin: 0px 0px 24px;
    min-width: 0px;
    font-size: 36px;
    line-height: 1.15;
    letter-spacing: -0.02em;
    font-weight: 600;
}
.bid-container .fadded-sub-title{
    margin: 0px 0px 8px;
    font-size: 18px;
    font-weight: 600;
    color: #666666;
}
.bid-container .bold-value{
    margin: 0px 0px 32px;
    min-width: 0px;
    font-size: 18px;
    font-weight: 600;
}

.bid-container .btn-bidding{
    width: 100%;
    padding: 5% 0;
    background: #4e61ff;
    color: white;
    border-radius: 10px;
    border: none;
    margin: 5px 0 0 0;

}



@media screen and (min-width: 52em){
    .bid-container .title {
        font-size: 46px;
    }
}
