.root {

}

.cover {
    display: flex;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
    align-items: center;
    justify-content: center;
}

.cover .media {
    height: 66vh;
}

.cover .img {
    width: auto;
}

.cover .video {
    width: auto;
}

.content .row-2 {
    display: grid;
    gap: 32px;
    grid-template-columns: repeat(1, 1fr);
}

.left-container{
    flex: auto;
    margin-left: auto;
    width: 100%;
    z-index: 3;
}
.insdie-left-container{
    display: grid;
    grid-gap: 32px;
    position: relative;
    z-index: 5;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.content .artwork-details-container  {
}
.content .artwork-details-container .title {
    margin: 24px 0px;
    min-width: 0px;
    font-size: 36px;
    line-height: 1.15;
    letter-spacing: -0.02em;
    font-weight: 600;
    overflow-wrap: break-word;
    hyphens: auto;
}
.content .artwork-details-container .artwork-info {
    display: flex;
    cursor: pointer;
    -webkit-box-align: center;
    align-items: center;
    color: #B3B3B3;
    box-sizing: border-box;
    margin: 8px 0px 0px;
    padding-bottom: 32px;

}

.content .artwork-details {
    box-sizing: border-box;
    margin: 0px;
    min-width: 0px;
    display: grid;
    gap: 32px;
}


.content .artwork-details .sub-header {
    font-size: 16px;
    font-weight: 600;
    margin: 0px 0px 8px;
}

.content .artwork-details .text-big {
    line-height: 1.15;
    letter-spacing: -0.02em;
    font-weight: 600;
    max-width: 27rem;
}

.content .artwork-details .external-links {
    display: grid;
    gap: 8px;
    max-width: 400px;
}

.content .artwork-details .external-link {
    background-color: #FFFFFF;
    position: relative;
    display: flex;
    box-shadow: rgb(0 0 0 / 5%) 0px 10px 20px;
    border-radius: 10px;
    padding: 20px;
    -webkit-box-align: center;
    align-items: center;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    will-change: transform;
    text-decoration: none;
    color: #B3B3B3;
    cursor: pointer;
    z-index: 1;
}

.content .artwork-details .external-link .text {
    font-size: 16px;
    font-weight: 600;
    color: #000000;
    margin: 0px 0px 0px 16px;
}

.content .artwork-details .external-link .exit {
    box-sizing: border-box;
    margin: 0px 0px 0px auto;
    min-width: 0px;
}

.auction-details-container {
    display: grid;
    gap: 32px;
    box-shadow: rgb(0 0 0 / 5%) 0px 10px 20px;
    border-radius: 10px;
}

.auction-details {
    display: grid;
    gap: 48px;
    grid-template-columns: auto 1fr;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 32px;
}

.auction-details .left-detail {
    padding-right: 32px;
    border-right-width: 1px;
    border-right-style: solid;
    border-color: var(--theme-ui-colors-black-10, #E6E6E6);
}

.auction-details  .mini-text {
    font-size: 16px;
    font-weight: 600;
    margin: 0px 0px 5px;
}

.auction-details  .big-text {
    font-weight: 600;
    font-size: 24px;
    margin: 0px 0px 10px;
}

.auction-details .left-detail .faded-text {
    font-size: 18px;
    font-weight: 600;
    color: #666666;
}

.auction-details-container .footer {
    padding: 32px;
    border-top-width: 1px;
    border-top-style: solid;
    border-color: #E6E6E6;
}

.auction-details-container .footer .footer-link {
    display: block;
    text-decoration: none;
}


.auction-details-container .footer .bg-btn {
    display: inline-block;
    text-align: center;
    font-weight: 600;
    padding: 16px 32px;
    border-radius: 15px;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
    cursor: pointer;
    outline: none;
    background-color: #000000;
    color: #FFFFFF;
    border-width: 2px;
    border-style: solid;
    border-image: initial;
    border-color: #1A1A1A;
    min-height: 60px;
    width: 100%;
}

.artwork-history-list{
    display: grid;
    gap: 8px;
}

@media screen and (min-width: 72em) {
    .content{
        width: 90%;
        margin: 0 auto;
    }
    .auction-details .left-detail .big-text {
        font-size: 36px;
    }
}

@media screen and (min-width: 64em) {

    .content .row-2 {
        gap: 48px;
    }
    .content .artwork-details .title {
        margin-bottom: 0px;
    }
}

@media screen and (min-width: 52em) {
  
    .content .row-2 {
        grid-template-columns: repeat(2, 1fr);
    }
    .content .artwork-details {
        gap: 48px;
    }
    .content .artwork-details .title {
        font-size: 46px;
    }
    .content .artwork-details .text-big {
        font-size: 46px;
    }
    .auction-details .footer .footer-link button {
        padding: 16px 24px
    }
    .artwork-history-list{
        gap: 16px;
    }
    

}