.navbar-nav li {
  padding: 12px 8px;
  border-radius: 10px;
  margin-left: 2px;
  margin-right: 2px;
}

.navbar-nav ul li a {
  text-decoration: none;
  color: #000;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  margin-left: 2px;
  margin-right: 2px;
  font-size: 18px;
  font-weight: 600;
  padding: 12px 10px;
  border-radius: 10px;
}

.navbar-nav ul li a:hover {
  background-color: #f2f2f2;
  color: #000;
}

.navbar-nav ul li a.active {
  background-color:var(--primary-color);
  color: #fff
}

.navbar-nav .connect-button {
  background-color: var(--primary-color);;
  color: #fff;
  padding: 12px 18px;
  border-radius: 50px;
}

.navbar-nav .user-btn {
  background-color: #FFFFFF;
  padding: 6px 6px 6px 24px;
  border-radius: 999px;
}

.navbar-nav .user-btn-content {
  margin: 0px 16px 0px 0px;
  min-width: 0px;
  display: flex;
  text-align: right;
  flex-direction: column;
  align-items: flex-end;
}

.navbar-nav .user-btn-content h6 {
  margin: 0 0 2px;
  font-size: 18px;
  font-weight: 600;
}

.navbar-nav .user-btn-content p {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
}

.navbar-nav .userAvatar {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  position: relative;
}

.navbar-nav .userAvatar .userAvatarContainer {
  box-sizing: border-box;
  margin: 0px;
  min-width: 42px;
  min-height: 42px;
  max-width: 42px;
  max-height: 42px;
  background-image: linear-gradient(135deg, rgb(84, 188, 251), rgb(67, 66, 243));
  background-repeat: initial;
  background-attachment: initial;
  background-origin: initial;
  background-clip: initial;
  background-color: #F2F2F2;
  background-size: cover;
  background-position: center center;
  border-radius: 999px;
}

.online-mark {
  margin: 0px;
  min-width: 0px;
  width: 12px;
  height: 12px;
  border-radius: 999px;
  border-width: 3px;
  border-style: solid;
  border-image: initial;
  border-color: var(--theme-ui-colors-white-100, #FFFFFF);
  box-sizing: content-box;
  position: absolute;
  bottom: 1px;
  left: -7px;
  background-color: var(--theme-ui-colors-utility-green, #24BE74);
}

.create-btn {
  margin-right: 15px;
  text-align: center;
  text-decoration: none;
  will-change: transform;
  font-weight: 600;
  padding: 16px 24px;
  cursor: pointer;
  outline: none;
  color: #FFFFFF;
  box-shadow: rgb(0 0 0 / 10%) 0px 10px 20px;
  border-radius: 999px;
  border: none;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
  font-size: 18px;
  min-height: 54px;
  max-height: 54px;
  background-color: rgb(207, 47, 249);
  /* background-image: url('../../../images/fnd-gradient.jpg'); */
  background-size: 100%;
  background-position: center bottom;
}
.bid-btn {
  margin-right: 15px;
  text-align: center;
  text-decoration: none;
  will-change: transform;
  font-weight: 600;
  padding: 16px 24px;
  cursor: pointer;
  outline: none;
  color: #000;
  box-shadow: rgb(0 0 0 / 10%) 0px 10px 20px;
  border-radius: 999px;
  border: 1px solid #eee;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1) 0s;
  font-size: 18px;
  min-height: 54px;
  max-height: 54px;
  background-size: 100%;
  background-position: center bottom;

}